.contacts {
  padding-block: 100px;
  background:    url(../../img/review-bg.webp) no-repeat center center fixed;
  
  &__title {
	margin-bottom: 27px;
  }
  
  &__inner {
	display:               grid;
	grid-template-columns: 1fr 1fr;
	gap:                   20px;
	@media(max-width: 992px) {
	  grid-template-columns: 1fr;
	}
  }
  
  &__left {
	display:        flex;
	flex-direction: column;
	gap:            clamp(20px, 2.5vw, 40px)
  }
  
  &__right {
	border-radius: 8px;
	overflow:      hidden;
	
	img {
	  border-radius: 8px;
	  height:        100%;
	}
  }
  
  &__item {
	display:               grid;
	grid-template-columns: 235px 1fr;
	gap:                   20px;
	
	@media(max-width: 767px) {
	  grid-template-columns: 1fr;
	}
	
	&:nth-child(1) {
	  border-bottom:  1px solid var(--hover-beige);
	  padding-bottom: 40px;
	}
	
	&-photo {
	  
	  @media(max-width: 992px) {
		height: 250px;
	  }
	  
	  img {
		border-radius: 8px;
		width:         100%;
		height:        100%;
	  }
	}
	
	&-content {
	  display:        flex;
	  flex-direction: column;
	  gap:            14px;
	  
	  svg {
		width:  14px;
		height: 14px;
		fill:   var(--beige);
	  }
	  
	  p {
		font-family:   var(--font-family);
		font-weight:   400;
		font-size:     16px;
		line-height:   130%;
		color:         var(--sv-gray);
		margin:        0;
		margin-bottom: 8px;
		display:       flex;
		align-items:   center;
		gap:           8px;
	  }
	  
	  .city {
		span {
		  font-family:    var(--second-family);
		  font-weight:    500;
		  font-size:      clamp(18px, 2.5vw, 24px);
		  line-height:    110%;
		  letter-spacing: 0.04em;
		  text-transform: uppercase;
		  color:          var(--sv-gray);
		}
	  }
	  
	  .phones-wrapper {
		display: flex;
		gap:     10px;
		
		a {
		  transition: color .3s ease-in-out;
		  @media(any-hover: hover) {
			&:hover {
			  color: var(--beige);
			}
		  }
		}
	  }
	  
	  .buttons {
		display: flex;
		gap:     10px;
		
		@media(max-width: 992px) {
		  flex-direction: column;
		  
		  button {
			width:     100%;
			max-width: 100%;
		  }
		}
	  }
	}
  }
}