.articles {
  margin-bottom: 80px;
  
  &__head {
	display:         flex;
	align-items:     center;
	justify-content: space-between;
	gap:             20px;
	margin-bottom:   clamp(20px, 2.5vw, 40px);
  }
  
  &__link {
	@media(max-width: 992px) {
	  display: none;
	}
  }
  
  &__title {
	margin-bottom: 0;
  }
  
  &__slider {
	max-width:      1720px;
	margin-left:    auto;
	padding-inline: 10px;
  }
  
  &__swiper {
	padding-block: 60px;
	
	@media(max-width: 992px) {
	  padding-block: 0;
	}
  }
  
  &__slide {
	box-sizing:     border-box;
	display:        flex;
	flex-direction: column;
	position:       relative;
	
	&:nth-child(even) {
	  transform: translateY(-40px);
	  @media(max-width: 992px) {
		transform: translateY(0);
	  }
	}
	
	&-link {
	  position: absolute;
	  inset:    0;
	  z-index:  1;
	}
	
	&-img {
	  position:      relative;
	  padding:       30%;
	  overflow:      hidden;
	  border-radius: 8px;
	  padding-top:   74%;
	  margin-bottom: 14px;
	  
	  img {
		position:      absolute;
		object-fit:    cover;
		width:         100%;
		height:        100%;
		top:           0;
		left:          0;
		border-radius: 8px;
		transition:    scale .3s ease-in-out;
	  }
	}
	
	&-date {
	  font-family:    var(--font-family);
	  font-weight:    400;
	  font-size:      12px;
	  line-height:    100%;
	  letter-spacing: 0.04em;
	  color:          var(--t-gray);
	  margin-bottom:  8px;
	}
	
	&-title {
	  font-family:    var(--second-family);
	  font-weight:    500;
	  font-size:      clamp(18px, 2vw, 20px);
	  line-height:    110%;
	  letter-spacing: 0.04em;
	  color:          var(--sv-gray);
	}
	
	&-bottom {
	  display:         flex;
	  justify-content: space-between;
	  gap:             10px;
	}
	
	&-toggler {
	  min-width: 40px;
	}
	
	@media(any-hover: hover) {
	  &:hover {
		img {
		  scale: 1.1;
		}
		
		.articles__slide-toggler {
		  background: var(--beige);
		  
		  &:before {
			opacity: 0;
		  }
		}
	  }
	}
  }
}