.projects {
  &__title {
	margin-bottom: 0;
  }
  
  &__head {
	display:         flex;
	align-items:     center;
	justify-content: space-between;
	gap:             20px;
	margin-bottom:   80px;
	flex-wrap: wrap;
	
	@media(max-width: 992px) {
	  margin-bottom: 30px;
	}
  }
  
  &__link {
	@media(max-width: 992px) {
	  max-width: 100%;
	  width: 100%;
	}
  }
  
  &__slider {
	max-width:   1730px;
	padding:     0 10px;
	margin-left: auto;
  }
  
  &__slide {
	position:       relative;
	box-sizing:     border-box;
	height:         auto;
	display:        flex;
	flex-direction: column;
	overflow:       hidden;
	
	&-link {
	  position: absolute;
	  inset:    0;
	  z-index:  2;
	}
	
	&-text {
	  font-family:    var(--second-family);
	  font-weight:    500;
	  font-size:      20px;
	  line-height:    110%;
	  letter-spacing: 0.04em;
	  color:          var(--sv-gray);
	  position:       absolute;
	  top:            20px;
	  left:           20px;
	  z-index:        1;
	}
	
	&-toggler {
	  position: absolute;
	  bottom:   0;
	  right:    0;
	}
	
	img {
	  border-radius: 8px;
	  width:      100%;
	}
	
	@media(any-hover: hover) {
	  &:hover {
		.projects__slide-toggler {
		  background: var(--beige);
		  
		  &:before {
			opacity: 0;
		  }
		}
	  }
	}
  }
}