html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  
}

.page {
  height:                   100%;
  font-family:              var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  
}

.page__body {
  margin:           0;
  min-width:        370px;
  min-height:       100%;
  font-size:        16px;
  overflow-x:       hidden;
  background-color: var(--black);
}

img {
  height:     auto;
  max-width:  100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border:           none;
  padding:          0;
  background-color: transparent;
  cursor:           pointer;
}

.list-reset {
  list-style: none;
  margin:     0;
  padding:    0;
}

.input-reset {
  -webkit-appearance: none;
  appearance:         none;
  border:             none;
  border-radius:      0;
  background-color:   #fff;
  
  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
	display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin:   -1px;
  border:   0;
  padding:  0;
  width:    1px;
  height:   1px;
  clip:     rect(0 0 0 0);
}

.container {
  margin:    0 auto;
  padding:   0 var(--container-offset);
  max-width: var(--container-width);
}


.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  left:                0;
  top:                 0;
  overflow:            hidden;
  width:               100%;
  height:              100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.hidden-sm {
  @media (max-width: 768px) {
	display: none !important;
  }
}

.icon-socials {
  width:         28px;
  height:        28px;
  display:       grid;
  place-items:   center;
  border-radius: 100px;
  background:    rgba(241, 241, 241, 0.4);
  transition:    .3s ease-in-out;
  position:      relative;
  overflow:      hidden;
  
  svg {
	width:    15px;
	height:   15px;
	position: relative;
	z-index:  1;
	fill:     var(--white);
  }
  
  &:before {
	content:    '';
	position:   absolute;
	bottom:     0;
	left:       0;
	width:      100%;
	height:     0;
	background: var(--beige);
	transition: .3s ease-in-out;
  }
  
  @media(any-hover: hover) {
	&:hover {
	  &:before {
		height: 28px;
	  }
	}
  }
}

.btn {
  font-family:     var(--font-family);
  font-weight:     400;
  font-size:       16px;
  line-height:     130%;
  color:           var(--sv-gray);
  padding:         8px 28px;
  background:      var(--beige);
  border-radius:   8px;
  display:         inline-flex;
  transition:      .3s ease-in-out;
  text-align:      center;
  justify-content: center;
  align-items:     center;
  @media(any-hover: hover) {
	&:hover {
	  background: var(--hover-beige);
	}
  }
}

.title__section {
  margin:         0;
  font-family:    var(--second-family);
  font-weight:    500;
  font-size:      clamp(28px, 3vw, 44px);
  line-height:    110%;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color:          var(--sv-gray);
  margin-bottom:  40px;
}

.link-border {
  font-family:     var(--font-family);
  font-weight:     400;
  font-size:       16px;
  line-height:     130%;
  color:           var(--sv-gray);
  border:          1px solid var(--beige);
  border-radius:   8px;
  padding:         12px;
  max-width:       250px;
  width:           100%;
  transition:      .3s ease-in-out;
  text-align:      center;
  display:         inline-flex;
  align-items:     center;
  justify-content: center;
  @media(any-hover: hover) {
	&:hover {
	  background: var(--hover-beige);
	}
  }
}

.toggler-arrow {
  width:         40px;
  height:        40px;
  border:        1px solid var(--beige);
  display:       grid;
  place-items:   center;
  border-radius: 8px;
  position:      relative;
  transition:    background-color .3s ease;
  
  &:before {
	content:    '';
	position:   absolute;
	width:      10px;
	height:     100%;
	left:       -3px;
	bottom:     0;
	background: var(--black);
	filter:     blur(2px);
	transition: .3s ease-in-out;
  }
}

.text-16 {
  font-family: var(--font-family);
  font-weight: 400;
  font-size:   clamp(14px, 2vw, 16px);
  line-height: 130%;
  color:       var(--sv-gray);
}

.input {
  border-radius: 8px;
  padding:       14px 18px;
  background:    rgba(241, 241, 241, 0.2);
  font-family:   var(--font-family);
  font-weight:   400;
  font-size:     16px;
  line-height:   130%;
  color:         rgba(241, 241, 241, 0.3);
  outline:       none;
  border:        1px solid transparent;
  transition:    .3s ease-in-out;
  
  &::placeholder {
	color:       inherit;
	font-family: inherit;
  }
  
  @media(any-hover: hover) {
	&:hover {
	  border-color: var(--hover-beige);
	}
  }
  
  &:focus {
	border-color: var(--hover-beige);
  }
}

.policy {
  position: relative;
  
  input {
	position:   absolute;
	z-index:    1;
	inset:      0;
	opacity:    0;
	appearance: none;
	cursor:     pointer;
  }
  
  label {
	font-family:    var(--font-family);
	font-weight:    400;
	font-size:      12px;
	line-height:    100%;
	letter-spacing: 0.04em;
	color:          var(--gray);
	display:        block;
	padding-left:   28px;
	
	&:before {
	  content:       '';
	  position:      absolute;
	  top:           0;
	  left:          0;
	  width:         20px;
	  height:        20px;
	  border:        1px solid var(--beige);
	  border-radius: 4px;
	  transition:    .3s ease-in-out;
	}
  }
  
  input:checked + label:before {
	background: url('../img/icons/checked.svg') center / contain no-repeat;
  }
}

.text-24 {
  font-family:    var(--second-family);
  font-weight:    500;
  font-size:      clamp(20px, 2vw, 24px);
  line-height:    110%;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color:          var(--sv-gray);
}

svg {
  transition: .3s ease-in-out;
}

p {
  font-family: var(--font-family);
  font-weight: 400;
  font-size:   16px;
  line-height: 130%;
  color:       var(--sv-gray);
}