.footer {
  padding-block: 40px;
  border-top:    1px solid var(--t-gray-bg);
  
  &__wrapper {
	display:         flex;
	justify-content: space-between;
	gap:             20px;
	flex-wrap:       wrap;
	
	@media(max-width: 992px) {
	  flex-direction: column;
	}
  }
  
  &__col {
	display:        flex;
	flex-direction: column;
  }
  
  .logo {
	max-width:     200px;
	display:       block;
	margin-bottom: 40px;
  }
  
  &__link {
	font-family:   var(--font-family);
	font-weight:   400;
	font-size:     14px;
	line-height:   100%;
	color:         var(--sv-gray);
	margin-bottom: 14px;
	@media(any-hover: hover) {
	  &:hover {
		color: var(--beige);
	  }
	}
  }
  
  &__btn {
	@media(max-width: 767px) {
	  width:     100%;
	  max-width: 100%;
	}
  }
  
  &__socials {
	display:       flex;
	gap:           10px;
	margin-bottom: 20px;
  }
  
  &__nav {
	display:               grid;
	grid-template-columns: 1fr 1fr;
	gap:                   clamp(20px, 2.5vw, 40px);
	
	&-title {
	  font-family:    var(--font-family);
	  font-weight:    400;
	  font-size:      14px;
	  line-height:    100%;
	  text-transform: uppercase;
	  color:          var(--sv-gray);
	  margin-bottom:  14px;
	}
	
	.nav {
	  .list {
		display:        flex;
		flex-direction: column;
		gap:            8px;
	  }
	  
	  a {
		font-family: var(--font-family);
		font-weight: 400;
		font-size:   clamp(12px, 2vw, 16px);
		line-height: 130%;
		color:       var(--t-gray);
		transition:  color 200ms ease;
		@media(any-hover: hover) {
		  &:hover {
			color: var(--hover-beige);
		  }
		}
	  }
	  
	  .footer__nav-link {
		text-transform: uppercase;
		color:          var(--sv-gray);
	  }
	}
  }
  
  &__end {
	display:               grid;
	grid-template-columns: 1fr 1fr;
	gap:                   clamp(20px, 2.5vw, 40px);
	
	@media(max-width: 767px) {
	  grid-template-columns: 1fr;
	  gap:                   10px;
	}
  }
  
  &__rec {
	display:        flex;
	flex-direction: column;
	gap:            10px;
	
	p {
	  font-family:    var(--font-family);
	  font-weight:    400;
	  font-size:      12px;
	  line-height:    100%;
	  letter-spacing: 0.04em;
	  color:          var(--t-gray);
	  margin:         0;
	}
  }
  
  &__policy {
	display:        flex;
	flex-direction: column;
	gap:            10px;
	
	a {
	  font-family:    var(--font-family);
	  font-weight:    400;
	  font-size:      12px;
	  line-height:    100%;
	  letter-spacing: 0.04em;
	  color:          var(--t-gray);
	  transition:     color 200ms ease;
	  
	  @media(any-hover: hover) {
		&:hover {
		  color: var(--beige);
		}
	  }
	}
  }
}