.offer {
  margin-bottom: 140px;
  @media(max-width: 992px) {
	margin-bottom: 80px;
  }
  
  &__title {
	margin-bottom: 0;
  }
  
  &__head {
	display:         flex;
	align-items:     center;
	justify-content: space-between;
	gap:             20px;
	margin-bottom:   80px;
	
	@media(max-width: 992px) {
	  margin-bottom: 30px;
	}
  }
  
  &__link {
	@media(max-width: 767px) {
	  display: none;
	}
  }
  
  &__items {
	display: grid;
	gap:     20px;
	
	
  }
  
  .offer__items--3 {
	grid-template-columns: repeat(3, 1fr);
	margin-bottom:         40px;
	@media(max-width: 992px) {
	  grid-template-columns: 1fr;
	}
	
	.offer__item {
	  &:nth-child(2) {
		transform: translateY(-40px);
		@media(max-width: 992px) {
		  transform: translateY(0);
		}
	  }
	}
  }
  
  .offer__items--4 {
	grid-template-columns: repeat(4, 1fr);
	@media(max-width: 992px) {
	  grid-template-columns: 1fr;
	}
	
	.offer__item {
	  &:nth-child(even) {
		transform: translateY(40px);
		@media(max-width: 992px) {
		  transform: translateY(0);
		}
	  }
	  
	  .offer__item-img {
		padding-top: 85%;
		
		@media(max-width: 992px) {
		  padding-top: 35%;
		}
	  }
	}
  }
  
  &__item {
	display:        flex;
	flex-direction: column;
	gap:            14px;
	position:       relative;
	
	&-link {
	  position: absolute;
	  inset:    0;
	  z-index:  1;
	}
	
	&-img {
	  position:      relative;
	  border-radius: 8px;
	  overflow:      hidden;
	  padding:       50%;
	  
	  img {
		position:      absolute;
		top:           0;
		left:          0;
		width:         100%;
		height:        100%;
		border-radius: 8px;
		transition:    scale .3s ease-in-out;
	  }
	}
	
	&-bottom {
	  display:         flex;
	  align-items:     center;
	  justify-content: space-between;
	  gap:             10px;
	}
	
	&-title {
	  font-family:    var(--second-family);
	  font-weight:    500;
	  font-size:      var(--fontSize24);
	  line-height:    110%;
	  letter-spacing: 0.04em;
	  text-transform: uppercase;
	  color:          var(--sv-gray);
	}
	
	&-toggler {
	  width:         40px;
	  height:        40px;
	  border:        1px solid var(--beige);
	  display:       grid;
	  place-items:   center;
	  border-radius: 8px;
	  position:      relative;
	  transition:    background-color .3s ease;
	  
	  &:before {
		content:    '';
		position:   absolute;
		width:      10px;
		height:     100%;
		left:       -3px;
		bottom:     0;
		background: var(--black);
		filter:     blur(2px);
		transition: .3s ease-in-out;
	  }
	}
	
	@media(any-hover: hover) {
	  &:hover {
		img {
		  scale: 1.1;
		}
		
		.offer__item-toggler {
		  background-color: var(--beige);
		  
		  &:before {
			opacity: 0;
		  }
		}
	  }
	}
  }
}