.catalog .projects {
  margin-bottom: 120px;
  @media(max-width: 992px) {
	margin-bottom: 80px;
  }
}

.breadcrumbs {
  margin-top:    20px;
  margin-bottom: 60px;
  
  ul {
	display:     flex;
	gap:         16px;
	flex-wrap:   wrap;
	align-items: center;
	
	a {
	  font-family:    var(--font-family);
	  font-weight:    400;
	  font-size:      12px;
	  line-height:    100%;
	  letter-spacing: 0.04em;
	  color:          var(--gray);
	  position:       relative;
	  padding-right:  10px;
	  transition:     color .2s ease-in-out;
	  @media(any-hover: hover) {
		&:hover {
		  color: var(--beige);
		}
	  }
	  
	  &:after {
		content:         '';
		position:        absolute;
		top:             50%;
		right:           -6px;
		transform:       translateY(-50%);
		width:           10px;
		height:          10px;
		background:      url('../img/icons/arrow-breadcrumbs.svg') no-repeat;
		background-size: contain;
	  }
	}
	
	span {
	  font-family:    var(--font-family);
	  font-weight:    400;
	  font-size:      12px;
	  line-height:    100%;
	  letter-spacing: 0.04em;
	  color:          var(--t-gray);
	}
	
  }
}

.contacts-main, .main-kitchen {
  padding-top: 120px
}

.page__title {
  font-family:     var(--second-family);
  font-weight:     500;
  font-size:       clamp(36px, 4vw, 60px);
  line-height:     110%;
  letter-spacing:  0.04em;
  text-transform:  uppercase;
  color:           var(--white);
  margin-bottom:   30px;
  display:         flex;
  justify-content: space-between;
  align-items:     center;
  gap:             20px;
  flex-wrap:       wrap;
  
  a {
	text-transform: initial;
  }
}

.ur-info {
  margin-bottom: 120px;
  @media(max-width: 992px) {
	margin-bottom: 80px;
  }
  
  &__wrapper {
	border-radius:   8px;
	padding:         40px;
	background:      var(--t-gray-bg);
	display:         flex;
	justify-content: space-between;
	gap:             20px;
	flex-wrap:       wrap;
  }
  
  .info {
	display:         flex;
	flex-direction:  column;
	gap:             14px;
	align-items:     end;
	justify-content: end;
	
	@media(max-width: 992px) {
	  justify-content: start;
	  align-items:     start;
	}
	
	p {
	  font-family:    var(--second-family);
	  font-weight:    500;
	  font-size:      20px;
	  line-height:    110%;
	  letter-spacing: 0.04em;
	  text-align:     right;
	  color:          var(--white);
	  margin:         0;
	  
	  @media(max-width: 992px) {
		text-align: left;
	  }
	}
	
	span {
	  font-family: var(--font-family);
	  font-weight: 400;
	  font-size:   16px;
	  line-height: 130%;
	  text-align:  right;
	  
	  color:       var(--white);
	  @media(max-width: 992px) {
		text-align: left;
	  }
	}
	
  }
}

.contacts-main .faq {
  margin-bottom: 80px;
}

.blog {
  margin-bottom: 120px;
  @media(max-width: 992px) {
	margin-bottom: 80px;
  }
  
  &__items {
	display:               grid;
	grid-template-columns: repeat(auto-fit, minmax(490px, 1fr));
	gap:                   20px;
	margin-bottom:         clamp(30px, 2.5vw, 40px);
	
	@media(max-width: 767px) {
	  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
	}
  }
  
  &__item {
	display:        flex;
	flex-direction: column;
	gap:            14px;
	overflow:       hidden;
	border-radius:  8px;
	position:       relative;
	
	&-date {
	  font-family:    var(--font-family);
	  font-weight:    400;
	  font-size:      12px;
	  line-height:    100%;
	  letter-spacing: 0.04em;
	  color:          var(--t-gray);
	}
	
	&-img {
	  padding-top: 25vw;
	  position:    relative;
	  
	  img {
		border-radius: 8px;
		position:      absolute;
		top:           0;
		left:          0;
		width:         100%;
		height:        100%;
		transition:    scale .2s ease-in-out;
	  }
	  
	  @media(max-width: 767px) {
		padding-top: 75%;
	  }
	}
	
	&-link {
	  position: absolute;
	  inset:    0;
	  z-index:  1;
	}
	
	&-title {
	  font-family:    var(--second-family);
	  font-weight:    500;
	  font-size:      clamp(16px, 2vw, 20px);
	  line-height:    110%;
	  letter-spacing: 0.04em;
	  color:          var(--sv-gray);
	}
	
	&-bottom {
	  display:         flex;
	  gap:             10px;
	  justify-content: space-between;
	}
	
	&-toggler {
	  min-width: 40px;
	  border: none;
	}
	
	@media(any-hover: hover) {
	  &:hover {
		img {
		  scale: 1.1;
		}
		
		.blog__item-toggler {
		  background: var(--beige);
		  
		  &:before {
			opacity: 0;
		  }
		}
	  }
	  
	}
  }
  
  &__pagination {
	display:         flex;
	gap:             8px;
	justify-content: center;
	
	&-btn {
	  border-radius: 8px;
	  width:         40px;
	  height:        40px;
	  display:       grid;
	  place-items:   center;
	  transition:    background-color .2s ease-in-out;
	  border:        1px solid var(--beige);
	  cursor:        pointer;
	  @media(any-hover: hover) {
		&:hover {
		  background: var(--beige);
		}
	  }
	}
	
	&-pages {
	  display: flex;
	  gap:     8px;
	}
	
	&-page {
	  border-radius: 8px;
	  width:         40px;
	  height:        40px;
	  display:       grid;
	  place-items:   center;
	  transition:    background-color .2s ease-in-out;
	  border:        1px solid var(--beige);
	  
	  &.active {
		background: var(--beige);
	  }
	  
	  @media(any-hover: hover) {
		&:hover {
		  background: var(--beige);
		}
	  }
	}
  }
}