.cb {
  margin-bottom: 80px;
  
  &__wrapper {
	position:        relative;
	display:         flex;
	flex-direction:  column;
	justify-content: center;
	min-height:      544px;
  }
  
  &__image {
	position: absolute;
	right:    0;
	top:      0;
	height:   544px;
	z-index:  -1;
	@media(max-width: 992px) {
	  position:   static;
	  height:     300px;
	  margin-top: 10px;
	}
	
	img {
	  width:         100%;
	  height:        100%;
	  border-radius: 8px;
	}
  }
  
  &__form {
	border-radius:  8px;
	padding:        40px;
	background:     var(--t-gray-bg);
	display:        flex;
	flex-direction: column;
	max-width:      750px;
	@media(max-width: 992px) {
	  padding: 20px 10px;
	}
  }
  
  &__title {
	margin-bottom: 14px;
  }
  
  &__desc {
	margin-bottom: clamp(30px, 2.5vw, 40px);
  }
}

.form-cb {
  display:        flex;
  flex-direction: column;
  gap:            20px;
  
  &__row {
	display:   flex;
	flex-wrap: wrap;
	gap:       10px;
	
	input {
	  flex: 1 1 330px;
	}
  }
  
  &__bottom {
	display:               grid;
	grid-template-columns: 1fr 1fr;
	align-items:           center;
	gap:                   20px;
	@media(max-width: 767px) {
	  grid-template-columns: 1fr;
	}
  }
  
  &__btn {
	padding: 12px;
  }
}