.kitchen {
  margin-bottom: 120px;
  @media(max-width: 992px) {
	margin-bottom: 80px;
  }
  
  &__items {
	display:   flex;
	flex-wrap: wrap;
	gap:       20px;
  }
  
  &__item {
	position:       relative;
	display:        flex;
	flex-direction: column;
	overflow:       hidden;
	padding:        20px;
	border-radius:  8px;
	flex:           1 1 495px;
	cursor:         pointer;
	
	&:nth-child(4) {
	  flex: 1 1 750px;
	}
	
	&:nth-child(5) {
	  flex: 1 1 750px;
	}
	
	&-img {
	  position: absolute;
	  inset:    0;
	  z-index:  -1;
	  
	  img {
		width:         100%;
		height:        100%;
		transition:    scale .2s ease-in-out;
		border-radius: 8px;
	  }
	}
	
	&-title {
	  padding-top: 80%;
	}
	
	@media(any-hover: hover) {
	  &:hover {
		img {
		  scale: 1.1;
		}
	  }
	}
  }
}