.hero {
  height:        100vh;
  position:      relative;
  padding-top:   200px;
  margin-bottom: 80px;
  
  
  &__bg {
	position: absolute;
	inset:    0;
	z-index:  -1;
	
	img {
	  width:  100%;
	  height: 100%;
	}
  }
  
  .container {
	position: relative;
	height:   100%;
  }
  
  &__content {
	display:        flex;
	flex-direction: column;
	max-width:      950px;
  }
  
  &__title {
	font-family:    var(--second-family);
	font-weight:    500;
	font-size:      clamp(36px, calc(36px + 24 * (100vw / 1920)), 60px);
	line-height:    110%;
	letter-spacing: 0.04em;
	text-transform: uppercase;
	color:          var(--white);
	margin:         0;
	margin-bottom:  20px;
  }
  
  &__desc {
	font-family:    var(--second-family);
	font-weight:    500;
	font-size:      clamp(20px, 2vw, 24px);
	line-height:    110%;
	letter-spacing: 0.04em;
	text-transform: uppercase;
	color:          var(--white);
	margin-bottom:  clamp(30px, 2vw, 40px);
  }
  
  &__btn {
	max-width:     240px;
	padding-block: 12px;
	@media(max-width: 767px) {
	  max-width: 100%;
	}
  }
  
  .animate-link {
	position:      absolute;
	width:         135px;
	height:        135px;
	border-radius: 100%;
	display:       grid;
	place-items:   center;
	background:    var(--beige);
	transition:    background-color .3s;
	bottom:        -50px;
	right:         0;
	@media(max-width: 767px) {
	  width:  120px;
	  height: 120px;
	  right:  20px;
	}
	
	&:before {
	  content:         '';
	  width:           114px;
	  height:          114px;
	  position:        absolute;
	  top:             50%;
	  left:            50%;
	  transform:       translate(-50%, -50%);
	  background:      url('../../img/text-circle.svg') center no-repeat;
	  background-size: cover;
	  animation:       rotateCircle 5s linear infinite;
	}
	
	@media(any-hover: hover) {
	  &:hover {
		background: var(--hover-beige);
	  }
	}
  }
  
  
}

@keyframes rotateCircle {
  from {
	transform: translate(-50%, -50%) rotate(0deg);
  }
  to {
	transform: translate(-50%, -50%) rotate(360deg);
  }
}

.hero-catalog {
  height: 600px;
}