.faq {
  
  &__items {
	display:        flex;
	flex-direction: column;
	gap:            10px;
	margin-bottom:  60px;
  }
  
  &__item {
	border-radius: 8px;
	padding:       20px;
	background:    linear-gradient(180deg, rgba(48, 48, 49, 0.5) 0%, #1b1b1c 100%);
	
	&-head {
	  display:     flex;
	  align-items: center;
	  gap:         14px;
	  cursor:      pointer;
	  
	  & > * {
		pointer-events: none;
	  }
	}
	
	&-number {
	  font-family:    var(--second-family);
	  font-weight:    500;
	  font-size:      32px;
	  line-height:    80%;
	  letter-spacing: 0.04em;
	  text-transform: uppercase;
	  color:          var(--sv-gray);
	  width:          48px;
	  height:         48px;
	  @media(max-width: 992px) {
		display: none;
	  }
	  &:before {
		display: none;
	  }
	}
	
	&-toggler {
	  margin-left: auto;
	  min-width: 40px;
	  &:before {
		display: none;
	  }
	}
	
	&-bottom {
	  display:            grid;
	  grid-template-rows: 0fr;
	  transition:         .3s ease-in-out;
	  overflow:           hidden;
	}
	
	&-content {
	  margin-top:  0; // 20px
	  overflow:    hidden;
	  padding-top: 14px;
	  border-top:  1px solid transparent; // rgba(241, 241, 241, 0.2)
	  transition:  .3s ease-in-out;
	  
	  ul {
		display:        flex;
		flex-direction: column;
		gap:            14px;
		margin-bottom:  20px;
	  }
	  
	  p {
		font-family: var(--font-family);
		font-weight: 400;
		font-size:   16px;
		line-height: 130%;
		color:       var(--sv-gray);
	  }
	  
	  li {
		font-family:  var(--font-family);
		font-weight:  400;
		font-size:    16px;
		line-height:  130%;
		color:        var(--sv-gray);
		position:     relative;
		padding-left: 24px;
		
		&:before {
		  content:    '';
		  position:   absolute;
		  top:        0;
		  left:       0;
		  width:      16px;
		  height:     16px;
		  background: url('../../img/icons/radio-icon.svg') no-repeat;
		}
	  }
	}
	
	&-btn {
	  padding-block: 12px;
	  margin-top:    20px;
	  
	  @media(max-width: 992px) {
		width: 100%;
	  }
	}
  }
  
  .faq__item--active {
	.faq__item-bottom {
	  grid-template-rows: 1fr;
	}
	
	.faq__item-content {
	  margin-top:   20px;
	  border-color: rgba(241, 241, 241, 0.2);
	}
	
	.faq__item-toggler {
	  background: var(--beige);
	  
	  svg {
		rotate: 90deg;
	  }
	}
  }
  
  &__callback {
	position:      relative;
	background:    var(--t-gray-bg);
	border-radius: 8px;
	padding:       40px;
	@media(max-width: 992px) {
	  padding: 20px 10px;
	}
	
	&-content {
	  max-width: 710px;
	  position:  relative;
	  z-index:   1;
	}
	
	&-title {
	  margin-bottom: 14px;
	}
	
	&-desc {
	  margin-bottom: 40px;
	}
	
	&-img {
	  position:  absolute;
	  bottom:    0;
	  right:     -40px;
	  max-width: 830px;
	  width:     45vw;
	  @media(max-width: 1560px) {
		right: 0;
	  }
	  @media(max-width: 992px) {
		display: none;
	  }
	  
	  img {
		display: block;
	  }
	}
	
	.form-cb__row input {
	  flex: 1 1 250px;
	}
  }
}