.reviews {
  padding-block: 100px;
  background:    url('../../img/review-bg.webp') no-repeat center center fixed;
  
  &__head {
	display:         flex;
	justify-content: space-between;
	align-items:     center;
	gap:             20px;
	margin-bottom:   14px;
  }
  
  &__title {
	margin-bottom: 0;
  }
  
  &__desc {
	margin-bottom: 40px;
	color:         var(--sv-gray);
  }
  
  &__nav {
	display: flex;
	gap:     10px;
	@media(max-width: 992px) {
	  display: none;
	}
	&-btn {
	  border-radius: 8px;
	  width:         40px;
	  height:        40px;
	  border:        1px solid var(--beige);
	  display:       grid;
	  place-items:   center;
	  cursor:        pointer;
	  transition: .3s ease-in-out;
	  
	  &.next {
		background: var(--beige);
	  }
	  
	  @media(any-hover: hover) {
		&:hover {
		  background: var(--hover-beige);
		}
	  }
	}
  }
  
  &__slide {
	position:        relative;
	display:         flex;
	align-items:     center;
	justify-content: center;
	flex-direction:  column;
	border-radius:   8px;
	padding:         60px 20px;
	background:      linear-gradient(180deg, rgba(48, 48, 49, 0.5) 0%, #1b1b1c 100%);
	box-sizing:      border-box;
	text-align:      center;
	height:          auto;
	
	p {
	  max-width: 380px;
	  margin:    0 auto;
	}
	
	&:before {
	  content:    '';
	  position:   absolute;
	  top:        10px;
	  left:       10px;
	  width:      44px;
	  height:     38px;
	  background: url('../../img/icons/icon-review1.svg') center / cover no-repeat;
	}
	
	&:after {
	  content:    '';
	  position:   absolute;
	  bottom:     10px;
	  right:      10px;
	  width:      44px;
	  height:     38px;
	  background: url('../../img/icons/icon-review2.svg') center / cover no-repeat;
	}
  }
}