.header {
  padding-top:    10px;
  padding-bottom: 10px;
  position:       fixed;
  top:            0;
  left:           0;
  width:          100%;
  z-index:        1000;
  transition:     background-color .2s ease;
  
  @media(max-width: 992px) {
	padding-bottom:    0;
  }
  &__top {
	padding-bottom: 10px;
	border-bottom:  1px solid var(--beige);
	margin-bottom:  10px;
	@media(max-width: 992px) {
	  margin-bottom: 0;
	}
	&-wrapper {
	  display:         flex;
	  align-items:     center;
	  gap:             20px;
	  justify-content: space-between;
	}
	
	&-actions {
	  display:     flex;
	  align-items: center;
	  gap:         20px;
	}
	
	.link {
	  font-family: var(--font-family);
	  font-weight: 400;
	  font-size:   14px;
	  line-height: 100%;
	  color:       var(--sv-gray);
	  transition:  color .3s ease-in-out;
	  @media(max-width: 992px) {
		display: none;
	  }
	  @media(any-hover: hover) {
		&:hover {
		  color: var(--hover-beige);
		}
	  }
	}
	
	&-socials {
	  display: flex;
	  gap:     10px;
	}
  }
  
  .left {
	display:     flex;
	align-items: center;
	gap:         20px;
	@media(max-width: 992px) {
	  flex: 1 1 auto;
	}
	.city {
	  font-family:              var(--third-family);
	  font-weight:              400;
	  font-size:                14px;
	  line-height:              100%;
	  text-decoration:          underline;
	  text-decoration-skip-ink: none;
	  color:                    var(--sv-gray);
	  @media(max-width: 767px) {
		display: none;
	  }
	}
  }
  
  &__menu {
	&-list {
	  display: flex;
	  gap:     clamp(20px, 2.5vw, 40px);
	}
	
	&-link {
	  font-family:    var(--font-family);
	  font-weight:    400;
	  font-size:      14px;
	  line-height:    100%;
	  text-transform: uppercase;
	  color:          var(--sv-gray);
	  transition:     color .3s ease-in-out;
	  @media(any-hover: hover) {
		&:hover {
		  color: var(--hover-beige);
		}
	  }
	}
  }
  
  &__btn {
	@media(max-width: 767px) {
	  display: none;
	}
  }
  
  &__mobile {
	display: none;
	
	@media(max-width: 992px) {
	  display: flex;
	  flex-direction: column;
	  gap: 20px;
	  margin-top: 30px;
	  justify-content: center;
	  align-items: center;
	}
	
	&-links {
	  display: flex;
	  gap: 20px;
	  align-items: center;
	  font-family: var(--font-family);
	  font-weight: 400;
	  font-size: 14px;
	  line-height: 100%;
	  color: var(--sv-gray);
	  
	  a {
		color: inherit;
		font-family: inherit;
	  }
	}
	
	.btn {
	  width: 100%;
	}
  }
}

.burger {
  --burger-width: 30px;
  --burger-height: 20px;
  --burger-line-height: 2px;
  
  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #ffffff;
  background-color: transparent;
  cursor: pointer;
  display: none;
  @media(max-width: 1140px) {
	display: flex;
  }
  &::before,
  &::after {
	content: "";
	position: absolute;
	left: 0;
	width: 100%;
	height: var(--burger-line-height);
	background-color: currentColor;
	transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  }
  
  &::before {
	top: 0;
  }
  
  &::after {
	top: calc(100% - var(--burger-line-height));
  }
  
  &__line {
	position: absolute;
	left: 0;
	top: 50%;
	width: 100%;
	height: var(--burger-line-height);
	background-color: currentColor;
	transform: translateY(-50%);
	transition: transform 0.3s ease-in-out;
  }
  
  &--active {
	&::before {
	  top: 50%;
	  transform: rotate(45deg);
	  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
	}
	
	&::after {
	  top: 50%;
	  transform: rotate(-45deg);
	  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
	}
	
	.burger__line {
	  transform: scale(0);
	  transition: transform 0.3s ease-in-out;
	}
  }
}

@media(max-width: 992px) {
  .header__bottom {
	position: absolute;
	top: 62px;
	left: 0;
	width: 100%;
	height: 100vh;
	background: var(--black);
	padding-top: 20px;
	left: -100%;
	transition: left 0.3s ease-in-out;
	
	&.nav--active {
	  left: 0;
	}
  }
  
  .header__menu-list {
	display: flex;
	flex-direction: column;
  }
}