.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(20px);
  display: grid;
  place-items: center;
  overflow: auto;
  padding: 10px;
  opacity: 0;
  visibility: hidden;
  transition: all 0.1s ease-in-out;
  &.open-modal {
	opacity: 1;
	visibility: visible;
  }
  &__container {
	border-radius: 8px;
	padding: 60px 40px;
	background: var(--t-gray-bg);
	position: relative;
	max-width: 750px;
	
	@media(max-width: 767px) {
	  padding: 40px 10px;
	}
  }
  
  &__close {
	position: absolute;
	top: 10px;
	right: 10px;
	cursor: pointer;
	transition: opacity .2s ease;
	will-change: opacity;
	@media(any-hover: hover) {
	  &:hover {
		opacity: .5;
	  }
	}
  }
  
  &__title {
	margin-bottom: 14px;
  }
  
  &__desc {
	margin-bottom: 24px;
  }
  
  .modal-form {
	display: flex;
	flex-direction: column;
	gap: 10px;
	&__bottom {
	  display: grid;
	  grid-template-columns: 1fr 1fr;
	  gap: 20px;
	  margin-top: 10px;
	  align-items: center;
	  @media(max-width: 767px) {
		grid-template-columns: 1fr;
	  }
	  .btn {
		padding-block: 12px;
	  }
	}
  }
}