:root {
  // base
  --font-family: "Vela Sans", sans-serif;
  --second-family: "Roboto Flex", sans-serif;
  --third-family: "ABeeZee", sans-serif;
  --font3: "Roboto", sans-serif;
  --content-width: 1540px;
  --container-offset: 10px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --white: #fff;
  --sv-gray: #f1f1f1;
  --gray: #ddd;
  --t-gray: #787c82;
  --black: #1b1b1c;
  --t-gray-bg: #303031;
  --beige: #a18265;
  --hover-beige: #816245;
  
  // font
  
  --fontSize24: clamp(20px, 2vw, 24px);
}
