.block-socials {
  margin-bottom: 120px;
  @media(max-width: 992px) {
	margin-bottom: 80px;
  }
  
  &__inner {
	display:               grid;
	grid-template-columns: 1fr 1fr;
	gap:                   10px;
	
	@media(max-width: 992px) {
	  grid-template-columns: 1fr;
	}
  }
  
  &__item {
	position:       relative;
	display:        flex;
	flex-direction: column;
	padding:        20px;
	border-radius:  8px;
	overflow:       hidden;
	
	&-title {
	  margin-bottom: 14px;
	  max-width:     365px;
	}
	
	&-desc {
	  margin-bottom: 33px;
	  max-width:     385px;
	  flex:          1 1 auto;
	}
	
	&-img {
	  position: absolute;
	  inset:    0;
	  z-index:  -1;
	  
	  img {
		width:         100%;
		height:        100%;
		border-radius: 8px;
	  }
	}
	
	&-link {
	  display:       flex;
	  align-items:   center;
	  gap:           10px;
	  background:    rgba(241, 241, 241, 0.1);
	  border-radius: 8px;
	  padding:       8px 8px 8px 28px;
	  max-width:     fit-content;
	  transition:    .2s ease-in-out;
	  @media(any-hover: hover) {
		&:hover {
		  transform: translateX(10px);
		}
	  }
	  
	  .icon {
		width:         60px;
		height:        40px;
		border-radius: 6px;
		display:       grid;
		place-items:   center;
	  }
	  
	  &.youtube {
		.icon {
		  background: #F40000;
		}
	  }
	  
	  &.telegram {
		.icon {
		  background: #27A6E5;
		}
	  }
	}
	
  }
}
