.designer {
  position:        relative;
  height:          700px;
  margin-bottom:   260px;
  display:         flex;
  flex-direction:  column;
  justify-content: end;
  @media(max-width: 992px) {
	margin-bottom: 660px;
	height:        500px;
  }
  
  &__bg {
	position: absolute;
	inset:    0;
	z-index:  -1;
	
	img {
	  width:      100%;
	  height:     100%;
	  object-fit: cover;
	  display:    block;
	}
  }
  
  &__content {
	max-width:             1265px;
	margin:                auto;
	border-radius:         8px;
	background:            linear-gradient(180deg, #303031 0%, #1b1b1c 100%);
	padding:               clamp(20px, 2.5vw, 40px);
	display:               grid;
	grid-template-columns: 1fr 1fr;
	gap:                   clamp(20px, 2.5vw, 40px);
	transform:             translateY(140px);
	@media(max-width: 992px) {
	  grid-template-columns: 1fr;
	  transform:             translateY(600px);
	  padding:               20px 10px;
	}
	
	.left {
	  display:        flex;
	  flex-direction: column;
	  gap:            14px;
	}
  }
  
  &__title {
	margin-bottom: 0;
  }
  
  &__futures {
	display:               grid;
	grid-template-columns: repeat(2, 1fr);
	gap:                   10px;
	@media(max-width: 992px) {
	  grid-template-columns: 1fr;
	}
  }
  
  &__future {
	display:        flex;
	flex-direction: column;
	gap:            10px;
	border-radius:  8px;
	padding:        20px;
	background:     linear-gradient(180deg, rgba(48, 48, 49, 0.5) 0%, #1b1b1c 100%);
	
	&-icon {
	  position:       relative;
	  display:        flex;
	  flex-direction: column;
	  gap:            10px;
	  
	  svg {
		width:  40px;
		height: 33px;
		
		fill:   var(--beige);
	  }
	  
	  &:after {
		content:    '';
		width:      100%;
		height:     1px;
		background: url(../../img/line.svg) center no-repeat;
	  }
	}
	
	&-desc {
	  font-family:    var(--second-family);
	  font-weight:    500;
	  font-size:      clamp(16px, 2vw, 20px);
	  line-height:    110%;
	  letter-spacing: 0.04em;
	  color:          var(--sv-gray);
	}
  }
  
  &__btn {
	display:         flex;
	align-items:     center;
	justify-content: space-between;
	gap:             10px;
	border:          1px solid var(--beige);
	border-radius:   8px;
	padding:         4px 4px 4px 28px;
	margin-top:      auto;
	transition:      .3s ease-in-out;
	@media(any-hover: hover) {
	  &:hover {
		background: var(--beige);
	  }
	}
	
	.toggler-arrow {
	  background: var(--beige);
	  
	  &:before {
		display: none;
	  }
	}
  }
}