.futures {
  margin-bottom: 160px;
  @media(max-width: 992px) {
	margin-bottom: 80px;
  }
  &__wrapper {
	display: grid;
	grid-template-columns: 1fr 1fr;
	position: relative;
	
	@media(max-width: 1200px) {
	  grid-template-columns: 1fr;
	}
  }
  
  &__video {
	position: absolute;
	max-width: 880px;
	top: 0;
	left: 0;
	width: 50vw;
	@media(max-width: 1440px) {
	  height: auto;
	}
	
	@media(max-width: 1200px) {
	  position: relative;
	  width: 100%;
	  margin-bottom: 10px;
	  max-width: 100%;
	}
	img {
	  width: 100%;
	  height: 100%;
	  border-radius: 8px;
	}
	
	.btn-play {
	  position: absolute;
	  top: 50%;
	  left: 50%;
	  transform: translate(-50%, -50%);
	  border-radius: 1000px;
	  background: var(--beige);
	  width: 70px;
	  height: 70px;
	  display: grid;
	  place-items: center;
	  transition: .3s ease;
	  &:before {
		content: '';
		position: absolute;
		width: 110%;
		height: 110%;
		border: 1px solid var(--beige);
		border-radius: 1000px;
	  }
	  svg {
		fill: var(--white);
		width: 17px;
		height: 17px;
	  }
	  
	  @media(any-hover: hover) {
		&:hover {
		  background: var(--hover-beige);
		}
	  }
	}
  }
  
  &__content {
	grid-column-start: 2;
	position: relative;
	z-index: 1;
	padding-top: 60px;
	
	@media(max-width: 1560px) {
	  padding-top: 0;
	}
  }
  
  &__title {
	max-width: 620px;
	margin-left: auto;
	
	@media(max-width: 1200px) {
	  margin-left: 0;
	}
  }
  
  &__items {
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 10px;
	
	@media(max-width: 767px) {
	  grid-template-columns: 1fr;
	}
  }
  
  &__item {
	border-radius: 8px;
	padding: 20px;
	background: var(--t-gray-bg);
	display: flex;
	flex-direction: column;
	gap: 8px;
	
	&-title {
	  font-family: var(--second-family);
	  font-weight: 500;
	  font-size: clamp(20px, 2vw, 24px);
	  line-height: 110%;
	  letter-spacing: 0.04em;
	  text-transform: uppercase;
	  color: var(--sv-gray);
	  display: flex;
	  flex-direction: column;
	  gap: clamp(10px, 2vw, 20px);
	  &:after {
		content: '';
		width: 100%;
		height: 1px;
		background: url("../../img/line.svg") center no-repeat;
	  }
	}
	
	&-desc {
	  font-family: var(--font-family);
	  font-weight: 400;
	  font-size: clamp(14px, 2vw, 16px);
	  line-height: 130%;
	  color: var(--sv-gray);
	}
  }
}